#foot {
    width: 100%;

    .email {
        width: 100%;
        display: flex;

        & input {
            flex: 1;
            padding: 3px 5px;
            border: 1px solid #dee2e6;
            border-radius: 8px;
            font-family: 'Poppins', sans-serif;
            font-size: 1rem;
            transition: all 0.3s ease;
        }

        & button {
            border-radius: 5px;
        }
    }

    .foot-body {
        width: 100%;

        .message-list {
            width: 100%;
            display: flex;
            justify-content: space-around;

            & ol {
                list-style-type: none;

                & .stay {
                    display: flex;
                    justify-content: center;

                    & div{
                        width: 2em;
                        height: 2em;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        border: 1px solid #000;
                        margin: 0 1px;
                    }
                }
            }
        }
    }

    .foot-footer{
        width: 100%;
        text-align: center;
        box-sizing: border-box;
        padding: 2em 0;

        & ol{
            display: flex;
            list-style-type: none;
            justify-content: space-evenly;
        }
    }
}