#header {
    width: 100%;
    display: flex;
    height: 2em;

    .LOGO {
        text-align: center;
        width: 20em;
        line-height: 2em;
    }

    .input-div {
        flex: 1;
        display: flex;

        & input {
            width: 100%;
            padding: 12px 15px;
            border: 1px solid #dee2e6;
            border-radius: 8px;
            font-family: 'Poppins', sans-serif;
            font-size: 1rem;
            transition: all 0.3s ease;
        }

        .Post {
            text-align: center;
            width: 5em;
            line-height: 2em;
            margin-left: 1em;
        }

        .Post:hover {
            cursor: pointer;
        }
    }

    .Login {
        display: flex;
        line-height: 2em;
    }
}